  .card {
    background: #fff;
    border-radius: 2px;
    height: 175px;
    width: 175px;
    margin:5px;
    margin: 7px 15px 7px 15px;
    position: relative;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    padding: 5px;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    float:left;
  }

  @media(max-width:768px) {
    .card {
      height:165px;
      width:165px;
    }
  }

  @media(max-width:568px) {
    .card {
      height:100px;
      width:100px;
      margin:5px;
    }
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#015ca2;
    
  }
  .card > .img-container > img {
    width: 100%;
    margin-top: 15px;
  }
  
  .card:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-box-shadow: 0 3px 3px rgb(126, 123, 123), 0 3px 6px rgb(126, 123, 123);
            box-shadow: 0 3px 3px rgb(126, 123, 123), 0 3px 6px rgb(126, 123, 123);
  }



